import React, { useCallback, useEffect } from 'react';
import { connect } from 'react-redux';
// import { withRouter } from 'react-router-native';
import { compose } from 'redux';
import Card from '../components/Card';
import { originalImgPath } from '../global/config';
import { setDiscoverData, setMoviesPageActionCreator, setGenresData, setActiveMoviesGenresAC} from "../redux/discover-reducer";
import {setTypeActionCreator } from "../redux/details-reducer";
import Pagination from '../components/Pagination';
const MoviePage = ({moviesData, setDiscoverData, type, currentPage, setMoviesPageActionCreator, setActiveMoviesGenresAC, setGenresData, moviesGenres, moviesActiveGenres}) => {
    
    useEffect(()=>{
        setDiscoverData('movie', currentPage, moviesActiveGenres.string)
        
    },[currentPage, moviesActiveGenres])
    useEffect(()=>{
        document.title = 'Фильмы'
        setGenresData('movie')
    },[])
    const setGenresHandler = (id)=>{
        // console.log(id)
        setActiveMoviesGenresAC(id)
        // setDiscoverData('movie', currentPage, moviesActiveGenres.string)
        setMoviesPageActionCreator(1)
    }
    const setEmptyGenresHandler = ()=>{
        setActiveMoviesGenresAC('')
        setMoviesPageActionCreator(1)
    }
    return (
        <section>
            <div className="container-fluid">
                <h2 className="title">Фильмы</h2>
                {moviesGenres && <ul className='genres-list'>{moviesGenres.map(genre => { return <li key={genre.id} onClick={() => setGenresHandler(genre.id)} className={moviesActiveGenres.array.some(item => item === genre.id) ? 'is-active' : ''}>{genre.name}</li> })}
                    {moviesActiveGenres.array.length>0 && <li onClick={setEmptyGenresHandler} className='genres-list__clear'>Очистить все</li>}
                </ul>}
                <div className="main-grid">
                    {moviesData && moviesData.results.map(item => { return <Card key={item.id} titleId={item.id} setType={setTypeActionCreator} type={'movie'} src={item.poster_path} title={item.title || item.name} score={item.vote_average} date={item.release_date || item.first_air_date} /> })}
                </div>
                {moviesData && <Pagination currentPage={currentPage} pagesCount={moviesData.total_pages} setDiscoverData={setDiscoverData} discover='movie' setCurrentPage={setMoviesPageActionCreator} activeGenres={moviesActiveGenres.string} />}
            </div>

        </section>
    );
};

const mapStateToProps = (state)=>{
    return{
        moviesData: state.discover.moviesData,
        moviesGenres: state.discover.moviesGenres,
        moviesActiveGenres: state.discover.moviesActiveGenres,
        currentPage: state.discover.moviesPage,
        type: state.details.titleType,
    }
}
export default compose( 
    connect(mapStateToProps, {setDiscoverData, setTypeActionCreator, setMoviesPageActionCreator, setActiveMoviesGenresAC, setGenresData})
)(MoviePage)